import * as React from 'react'
import * as ReactDOM from 'react-dom'
import { useState, useContext, createContext, useReducer} from 'react'
import axios from 'axios'
/* 10項目の項目（スキーマ）を用意し、画面で入力したデータを登録、編集、削除するアプリを作成する。
一覧画面（index.html）を開くと登録したデータをテーブルで一覧表示する。
データが登録されていない場合は「登録されていない」旨を表示する。
index.htmlに「新規登録」ボタンを設置し、登録画面に遷移する。
登録画面で10項目を入力するフォームを用意する。
登録画面で「登録」ボタンを押下し、データを登録する。成功時には一覧画面に遷移する。一覧には登録した全データが表示されていること
登録に失敗した場合は、エラー内容をalertで表示すること
一覧から任意のデータを選択し、そのデータの編集画面に遷移する。
編集画面で選択したデータの編集が行えること
編集画面に「更新」ボタンを押下し、データを更新する。成功時に一覧画面に遷移する。一覧には編集後のデータが表示されていること
失敗時にはエラー内容をalertで表示すること
一覧から任意のデータを選択し、「削除」ボタンを押下することでそのデータを削除する。
削除成功時には削除後の一覧が表示されること */
type form =  any
const FormContext = createContext<form>([])
const ChangeTextContext = createContext<form>([])

const initState = {check: ''}
const reducer = (state:any, action:any) => {
    switch(action.type) {
        case 'check1':
        return {...state,check: state.check + 'チェック1,'}
        case 'check2':
            return {...state,check: state.check + 'チェック2,'}
        case 'check3':
            return {...state,check: state.check +  'チェック3,'}
        default: 
        return state
    }
}

const FormComponent = () => {
    const [name, email, memo, address, job, height, birthday] = useContext(FormContext)
    const [setName, setEmail, setJob, setAddress, setMemo, setHeight, setBirthday] = useContext(ChangeTextContext)
    console.log(birthday)
    return (
        <form name="forms">    
            <input type="text" name={name} value={name} onChange={(e:any) => setName(e.target.value)} placeholder="名前"　/><br />
            <input type="text" name={email} value={email} onChange={(e:any) => setEmail(e.target.value)} placeholder="メールアドレス" /><br />
            <label htmlFor="calendar">生年月日:</label><br />
            <input type="date" name="calendar" max="9999-12-31" value={birthday} onChange={(e:any) => setBirthday(e.target.value)}></input><br />
            <input type="text" value={job} onChange={(e:any) => setJob(e.target.value)} placeholder="職業" /><br />
            <input type="text" name={address} value={address} onChange={(e:any) => setAddress(e.target.value)} placeholder="住所" /><br />
            <label htmlFor={height}>身長</label><input type="text" name={height} value={height} onChange={(e:any) => setHeight(e.target.value)}/><br />
            <CheckComponent />
            <textarea name={memo} value={memo} onChange={(e:any) => setMemo(e.target.value)} placeholder="メモ"></textarea><br /> 
            <ButtonCompoent />
        </form>
    )
}

const CheckComponent = () => {
    const [gender] = useContext(FormContext)
    const [setSelectBox ,setGender] = useContext(ChangeTextContext)
    const [state, dispatch] = useReducer(reducer,initState)
    console.log(state)
    return (
    <form>
         <input type="radio" name={gender} onChange={() => setGender('男')} /><label htmlFor="gender">男</label>
         <input type="radio" name={gender} onChange={() => setGender('女')} /><label htmlFor="gender">女</label><br />
         <p>チェック<br />
          <input type="checkbox" name="check" value="check1" onChange={() => dispatch({type: 'check1'})} />チェック１
          <input type="checkbox" name="check" value="check2" onChange={() => dispatch({type: 'check2'})} />チェック2
          <input type="checkbox" name="check" value="check3" onChange={() => dispatch({type: 'check3'})} />チェック3
         </p>
         <select name="example" id="output" onChange={(select:any) => setSelectBox(select.value)}>
          <option>サンプル１</option>
          <option>サンプル２</option>
          <option>サンプル３</option>
        </select><br />
    </form>
    )
}

const ButtonCompoent = () => {
    const [putdata] = useContext(ChangeTextContext)
    return(
    <button type="submit"  onClick={putdata}>登録</button>
    )
}

const App = () => {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [memo, setMemo] = useState('')
    const [gender, setGender] = useState('')
    const [birthday, setBirthday] = useState('')
    const [job, setJob] = useState('')
    const [address, setAddress] = useState('')
    const [height, setHeight] = useState('')
    const [selectBox, setSelectBox] = useState('')
    const [state] = useReducer(reducer,initState)
   
    console.log(birthday)
    const req: VtecxApp.Entry[] = [
        {    
            user:{
                name: name,
                email: email,
                memo: memo,
                gender: gender,
                job: job,
                address: address,
                height:height,
                select: selectBox,
                check: state.check
            },
            link: [
                {
                    "___href": "/foo/2",
                    "___rel": "self"
                }
            ]
        }
    ]
  
    const putdata = async () => {
        try {
          axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
          const res = await axios.put('/d/foo',req)
          console.log(res)
          console.log(req)
        } catch (e) {
          alert('error')
          console.log(e)
        }
      }

    
  return (
      <div>
          <FormContext.Provider value={[name, email, memo, gender,address, job, height,selectBox, birthday]}>
              <ChangeTextContext.Provider value={[setName, setEmail, setJob, setAddress,
                 setMemo, setHeight, setGender, putdata, setSelectBox, setBirthday]}>        
                      <FormComponent />
              </ChangeTextContext.Provider>
          </FormContext.Provider>
      </div>
  )

}

ReactDOM.render(<App />, document.getElementById('container'))